import React from "react";
import { graphql, useStaticQuery } from "gatsby";

// Styles
import "./WeaningBenefits.styles.scss";

const WeaningBenefits = () => {

    const data = useStaticQuery(graphql`
    query {
        allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(weaning)/"}, frontmatter: {title: {eq: "Weaning Benefits"}}}) {
        edges {
          node {
            frontmatter {
              mainTitle
              subtitle
            }
            html
          }
        }
      }
    }
  `)
    
    return (
        <div>
            {
                data.allMarkdownRemark.edges.map(({node}) => (
                    <div className="weaning-benefits">
                        <h3>{ node.frontmatter.mainTitle }</h3>
                        <h4>{ node.frontmatter.subtitle }</h4>
                    </div>
                ))
            }
        </div>
    )
}

export default WeaningBenefits;