import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

// Styles
import "./weaning.styles.scss";

// Components
import WeaningBenefits from "../components/WeaningBenefits/WeaningBenefits.component";
import DividerArrow from "../components/DividerArrow/DividerArrow.component";
import SpanishFlag from "../components/DownloadAsset/SpanishFlag.component";
import "../components/DownloadAsset/DownloadAsset.styles.scss";


const Weaning = () => {

  const data = useStaticQuery(graphql`
    query {
      weaning: allMarkdownRemark(filter: {frontmatter: {title: {eq: "Weaning"}}}) {
        edges {
          node {
            frontmatter {
              title
              description
              subtitle
              headImage {
                childImageSharp {
                  fluid (maxWidth: 1920) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
              productImage {
                childImageSharp {
                  fluid (maxWidth: 1920) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
            html
          }
        }
      }
      weaningDownload: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(weaning/downloads)/"}}, sort: {fields: id, order: DESC}) {
        edges {
          node {
            frontmatter {
              mainTitle
              spanishLink
              englishLink
              productImage {
                childImageSharp {
                  fluid (maxWidth: 1920) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
            html
          }
        }
      }
    }
  `)
  
  return (
    <Layout>
      {
        data.weaning.edges.map(({node}) => (
          <div>
            <SEO title={ node.frontmatter.title } description={ node.frontmatter.description } />
            <Img fluid={ node.frontmatter.headImage.childImageSharp.fluid } />
            <div className="weaning">
              <h2>{ node.frontmatter.title }</h2>
              <p>{ node.frontmatter.subtitle }</p>
            </div>
            <WeaningBenefits />
            <div className="home-products">
                <div className="content" dangerouslySetInnerHTML={{ __html: node.html }} />
                <Img fluid={ node.frontmatter.productImage.childImageSharp.fluid } />
            </div>
          </div>
        ))
      }
      {
        data.weaningDownload.edges.map(({node}) => (
          <div className="download-asset-container">
            <DividerArrow />
            <div className="download-asset">
                <Img fluid={node.frontmatter.productImage.childImageSharp.fluid} />
                <div className="download-asset-text">
                    <h2>{ node.frontmatter.mainTitle }</h2>
                    <div dangerouslySetInnerHTML={{ __html: node.html }} />
                    <div className="download-asset-links">
                        <a href={ node.frontmatter.englishLink } target="_blank" rel="noopener noreferrer">Download PDF</a>
                        <a href={ node.frontmatter.spanishLink } target="_blank" rel="noopener noreferrer"><SpanishFlag />Also available in Spanish</a>
                    </div>
                </div>
            </div>
          </div>
        ))
      }
    </Layout>
  )
}

export default Weaning
